import React from 'react';
import HeaderStyle from './header.css';

function HeaderTitle () {
    return (
        <h1 className={HeaderStyle.title}>Gerador de tabela-verdade</h1>
    );
}

function HeaderDescription () {
    return (
        <p className={HeaderStyle.description}>
            Esta  uma ferramenta que automatiza a criao da tabela-verdade de expresses lgicas.
            Ao digitar, a tabela atualizar automaticamente e exibir todas as
            combinaes possveis. Alm disso, voc poder acompanhar progressivamente o
            desenvolvimento da expresso lgica atravs das sub-expresses. Voc poder utilizar
            diversos caracteres para compor a expresso lgica.
        </p>
    );
}

export default function Header () {
    return (
        <header>
            <HeaderTitle />
            <HeaderDescription />
        </header>
    );
}
